import uniPopup from "@/pages/components/uni-popup/uni-popup.vue"
export default {
  components: {
    uniPopup: uniPopup
  },
  data () {
    return {
      avatarUrl: '',
      sign: '',
      menuList: [{
        title: '用户昵称',
        name: 'nickName',
        content: ''
      }, {
        title: '性别',
        name: 'gender',
        content: ''
      }, {
        title: '个性签名',
        name: 'sign',
        content: ''
      }, {
        title: '绑定手机',
        name: 'mobile',
        content: '',
        path: '/pages/login/bindTel'
      }]
    }
  },
  onShow() {
    let that= this
    that.getUserInfo()
  },
  methods:{
    getUserInfo() {
      let that= this,
        params= {
          url: that.$api.getUserInfo
        }
      that.$httpRequest(params).then((res) => {
        let info= res.data.info,
          list= that.menuList
        list.map((obj, index) => {
          obj.content= info[obj.name]
        })
        that.avatarUrl= info.avatarUrl
        that.menuList= list
      })
    },
    clickAction(index, path) {
      let that= this
      if(index === 2) {
        that.open()
      } else if(index === 3) {
        uni.navigateTo({
          url: path
        })
        return
      }
    },
    // 打开弹窗
    open(){
      this.sign= this.menuList[2].content
      // 需要在 popup 组件，指定 ref 为 popup
      this.$refs.popup.open()
      
    },
    close() {
      this.$refs.popup.close()
    },
    // 确认修改个性签名
    sureEdit() {
      let that= this,
        params= {
          url: that.$api.editSign,
          data: {
            sign: that.sign
          }
        }
      that.$httpRequest(params).then(res => {
        console.log(res)
        that.menuList[2].content= that.sign
        that.close()
      })
    }
  }
}
